import React from "react";
import PropTypes from "prop-types";

const Radio = (props) => {

  return(
    <span className={props.className}>
      <input type="radio" className="radio-box" id={props.id} name={props.name} defaultChecked={props.defaultChecked} disabled={props.disabled}/>
      <label htmlFor={props.id} />
    </span>
  )
}

Radio.defaultProps = {
  id: 'rad0'
}

Radio.propTypes = {
  id: PropTypes.string
}

export default  Radio;