import React from "react";
import SyntaxHighlighter from "react-syntax-highlighter";
import { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';

import Layout from "../components/layout"
import Checkbox from "../components/ui/Checkbox";
import Radio from "../components/ui/Radio";
import Input from "../components/ui/Input";
import Select from "../components/ui/Select";

import ReceiptIcon from "../styles/img/svg/receipt.svg";
import ResizeIcon from "../styles/img/svg/resize.svg";
import CheckIcon from "../styles/img/svg/check.svg";
import SEO from "../components/seo"


export default class Forms extends React.Component{
  constructor(props) {
    super(props);

    this.state = {
      disabled: false
    }
  }

  handleDisableAllClick = () => {
    this.setState({
      disabled: !this.state.disabled
    })
  }

  render() {
    return(
      <Layout>
        <SEO title="Forms" />
        <div className="container">
          {/*--- BASIC FORM ELEMENTS ---*/}
          <div className="island px-4 py-3 mb-3">
            <h2>
              <ReceiptIcon className="mr-2" />
              Basic Form Elements
              <button className="btn btn-link mt-2 mt-sm-0 ml-auto" onClick={this.handleDisableAllClick}>{this.state.disabled ? "Enable" : "Disable"} all</button>
            </h2>
            <div className="mt-3 row">
              <div className="col-md">
                <div className="form-group">
                  <Input placeholder={"Placeholder"} label={"Regular Input"} disabled={this.state.disabled}/>
                </div>
              </div>
              <div className="col-md">
                <div className="form-group">
                  <label>Select</label>
                  <select className="form-control" disabled={this.state.disabled}>
                    <option>Please select</option>
                    <option value="1">Option 1</option>
                    <option value="2">Option 2</option>
                    <option value="3">Option 3</option>
                    <option value="4">Option 4</option>
                    <option value="5">Option 5</option>
                  </select>
                </div>
              </div>
              <div className="col-md">
                <div className="form-group">
                  <label className="d-block">Checkbox</label>
                  <Checkbox id={"cb1"} disabled={this.state.disabled} />
                  <Checkbox className="ml-3" id={"cb-2"} checked disabled={this.state.disabled} />
                </div>
              </div>
              <div className="col-md">
                <div className="form-group">
                  <label className="d-block">Radio</label>
                  <Radio name="radio" id={"rad1"} defaultChecked disabled={this.state.disabled} />
                  <Radio name="radio" id={"rad2"} className="ml-3" disabled={this.state.disabled} />
                </div>
              </div>
            </div>
            <div className="mt-3">
              <SyntaxHighlighter language="html" style={docco} className="rounded" showLineNumbers="true">
{`<div class="form-group">
  <label>Regular Input</label>
  <input class="form-control" type="text" placeholder="Placeholder" />
</div>
<div class="form-group">
  <label>Select</label>
  <select class="form-control">
    <option>Please select</option>
  </select>
</div>
<div class="form-group">
  <span>
    <input type="checkbox" class="check-box" id="cb1" />
    <label for="cb1"></label>
  </span>
</div>
<div class="form-group">
  <span>
    <input type="radio" class="radio-box" id="rad1" />
    <label for="rad1"></label>
  </span>
</div>`}
              </SyntaxHighlighter>
            </div>
          </div>
          {/*--- SIZING ---*/}
          <div className="island px-4 py-2 mb-3">
            <h2>
              <ResizeIcon className="mr-2" />
              Sizing
            </h2>
            <div className="mt-3 row">
              <div className="col">
                <div className="form-group">
                  <Input size={"lg"} placeholder={"Large input"} label={"Large input"} />
                </div>
                <div className="form-group">
                  <Input size={"sm"} placeholder={"Small input"} label={"Small input"} />
                </div>
              </div>
              <div className="col">
                <div className="form-group">
                  <Select size={"lg"} label={"Large select"} />
                </div>
                <div className="form-group">
                  <Select size={"sm"} label={"Small select"} />
                </div>
              </div>
            </div>
            <div className="mt-3">
<SyntaxHighlighter language="html" style={docco} className="rounded" showLineNumbers="true">
{`<input type="text" class="form-control form-control-lg" placeholder="Large input">
<input type="text" class="form-control form-control-lg" placeholder="Large input">

<select type="text" class="form-control form-control-lg">
  <option>Please select</option>
</select>
<select type="text" class="form-control form-control-lg">
  <option>Please select</option>
</select>`}
</SyntaxHighlighter>
            </div>
          </div>
          {/*--- VALIDATION ---*/}
          <div className="island px-4 py-3 mb-3">
            <h2>
              <CheckIcon className="mr-2" />
              Validation
            </h2>
            <div className="mt-3 row">
              <div className="col">
                <div className="form-group">
                  <Input placeholder={"Invalid input"} label={"Invalid input"} invalid={true} />
                </div>
              </div>
              <div className="col">
                <div className="form-group">
                  <Select label={"Invalid select"} invalid={true}/>
                </div>
              </div>
            </div>
            <div className="mt-3">
              <SyntaxHighlighter language="html" style={docco} className="rounded" showLineNumbers="true">
{`<input type="text" class="form-control is-invalid" placeholder="Invalid input">
<select type="text" class="form-control is-invalid">
  <option>Please select</option>
</select>`}
              </SyntaxHighlighter>
            </div>
          </div>
        </div>
      </Layout>
    )
  }
}